.logo {
  fill: $color-alice-blue;
  width: 70px;
  height: 38px;

  @include vp-1159 {
    width: 38px;
    height: 20px;
  }

  @include vp-767 {
    width: 42px;
    height: 22px;
  }

  &:hover,
  &:focus {
    fill: $color-ship-cove;
  }

  &:active {
    fill: $color-curiosus-blue;
  }
}

.logo svg {
  width: 70px;
  height: 38px;

  @include vp-1159 {
    width: 38px;
    height: 20px;
  }

  @include vp-767 {
    width: 42px;
    height: 22px;
  }
}
